html {
  scroll-behavior: smooth;
}

* {
  margin: 0;
  text-decoration: none;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  /* outline: 1px solid red; */
}

.overlay {
  background-color: rgba(0, 0, 0, 0.8);
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 0;
  z-index: 99999;

  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  overflow-x: hidden;
  transition: .9s ease;

}



.active {
  width: 100%;
}
/* @keyframes backgrounds {
  from{
    transform: translateY(-500px)
  }
  to{
    transform: translateY(10px);
  }
} */

.overlay a {
  font-size: 2rem;
  color: var(--i, #bbb);
  padding: 15px;
  /* animation-name: backgrounds;
  animation-duration: 3s;
  animation-delay: 0s;
  animation-iteration-count: 1; */
  /* animation: backgrounds 2s infinite ; */


}

.overlay a:hover {
  color: var(--i, #fff);
}

.fa-xmark {
  position: absolute;
  font-size: 3rem;
  right: 25px;
  top: 100px;
  color: white;
}

.fa-bars {
  display: none;
}


.header {
