}

.header__mouse-effect-mouse {
  /* margin-top:5% ; */

  animation: downUp 0.9s cubic-bezier(0.1, 0.25, 0.1, 1) 0s infinite alternate both;
}

@keyframes downUp {
  from {
    transform: translateY(-10px);
  }

  to {
    transform: translateY(5px);
  }
}


